@import "base/_variable";
.left-nav{
    position: fixed;
    z-index: 0;
    left: 0;
    top: 0px;
    border-right: 1px solid #ccc;
    padding: 100px 0 71px;
    width: 200px;
    height: 100%;
    box-sizing: border-box;
    overflow-y: auto;
    overflow-x: hidden;
    i{
        font-size:20px ;
        margin-right: 10px;
        width: 25px;
        text-align: center;
        vertical-align: top;
    }
    ul ul{display: none;}
    >ul>li.active{
        .nav-title{
            color: #176CBB;
            > .icon-dropdown:before{
                content: '\f107';
            }
        }
    } 
    li li{
        padding: 15px 20px 15px 40px;
        border-bottom: 1px solid #ccc;
        &:before,&.active:after{
            font-family: FontAwesome;
            font-weight: normal;
            font-style: normal;
            text-decoration: inherit;
            -webkit-font-smoothing: antialiased;
            *margin-right: .3em;
            text-decoration: inherit;
            display: inline-block;
            speak: none;
            vertical-align: middle;
            content: '\f0da';
            font-size: 20px;
            margin-right: 10px;
        }
        &:hover,&:active{
            color: #176CBB;
            cursor: pointer;
        }
        &.active,&.active:hover{
            background: #3892da;
            color: #fff;
            position: relative;
            border-left:2px solid #f34e30 ;
            cursor: default;
            &:before,&:active{
                color: #fff;
            }
        }
        &.active:after{
            content: '\f0d9';
            margin: 0;
            top: 50%;
            right: -1px;
            margin-top: -10px;
            position: absolute;
        }
    }
    
    .nav-title{     
        padding: 15px 20px;
        border-bottom: 1px solid #ccc;   
        font-size: 16px;     
        cursor: pointer;
        @include white-bg;   
        &:hover{
            color: #176CBB;
        }
        > .icon-dropdown:before{
            content: "\f105";
            font-size: 25px;
        }
    }
}
.nav-bar{
    border-bottom: 1px solid #ccc;
    padding: 10px 20px 0;
    &:before,&:after{
        display: table;
        line-height: 0;
        content: "";
    }
    &:after{
       clear: both;
    }
    
    li {
        border: 1px solid #ccc;  
        border-bottom:none ;
        @include white-bg;
        float: left;
        margin-right: 10px;
        border-radius: 2px;
        a{
            display: block;            
            padding: 10px 20px;
            cursor: pointer;
            
        }
        &:hover{
            background: #fff;
        }
        &.active{
            margin-bottom: -2px;
            background: #fff;
            a{
                border-top:2px solid #176CBB ;
                cursor: default;
            }
        }
        
    }
}
